import './Home.scss';
import Button from '../button/Button';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function Home() {

    window.scrollTo(0, 0)

    const settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1000,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 2
                }
            },
            {
                breakpoint: 700,
                settings: {
                    slidesToShow: 1
                }
            }
        ]
    };

    return (
        <div className="home">
            <header>
                <div className="container">
                    <h2>
                     ,  !
                </h2>
                    <p>
                        GrossBee  ,         .
                </p>
                    {/* <Button text='About us' /> */}
                </div>
            </header>

            <main>
                <section className='about'>
                    <div className="container">
                        <h2>
                             
                        </h2>
                        <p>
                                 IT-.        .  ,  ,         .
                        </p>
                        <p>
                               !               ?       .              ,       .
                        </p>
                    </div>

                    <div className="slider">
                        <Slider {...settings}>
                            {
                                [['', ' Landing ( ),          . Landing Page     .'], ['-', '  -     ,    ,  ,      .'], [' ', '     .             ,     ,    .'], [' ', ',   -  ,  ,   - !'], [' ', '         ,       .'], ['   ', '        ,    .     ,   ,      ,      .']].map(card =>
                                    <div className="card">
                                        <h6>
                                            {card[0]}
                                        </h6>
                                        <p>
                                            {card[1]}
                                        </p>
                                    </div>
                                )
                            }
                        </Slider>
                    </div>
                </section>

                <section className='contactUs'>
                    <div className="container">
                        <h2>
                                
                        </h2>
                        <Link to='/Project_2____GROSS_BEE/contact-us'>
                            <Button text='Contact us now' />
                        </Link>
                    </div>
                </section>
            </main>
        </div >
    )
}